import React from 'react'
import { useGetPokemonUrlQuery } from '../../../store/pokemonSlice';
import '../../../App.css'
export default function Pokemon({ name, url }) {
   const { data, error, isLoading } = useGetPokemonUrlQuery(url);
   let pokemonUrl = data?.sprites.front_shiny;
   return (
      <div className="pakemom">
         <p className="pakemon-text">{name}</p>
         <img src={pokemonUrl} alt="" />
      </div>
   )
}
