import React from 'react'
import { useState } from 'react';
import PokemonStore from '../PokemonStore/PokemonStore';
import '../../../src/App.css';



export default function Button() {
   let [value, getNewTag] = useState(false);

   return (
      <div className='block'>
         {value === false ? <button onClick={() => getNewTag(true)} className="button">Get pokemons</button>
            : <PokemonStore />}
      </div>
   )
}
