import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

export const fetchPokemon = createApi({
   reducerPath: 'fetchPokemon',
   baseQuery: fetchBaseQuery({ baseUrl: 'https://pokeapi.co/api/v2/' }),
   endpoints: (builder) => ({
      getPokemons: builder.query({
         query: () => `pokemon`,
      }),
      getPokemonUrl: builder.query({
         query: (url) => `${url}`
      }),
   }),
})

export const { useGetPokemonsQuery, useGetPokemonUrlQuery } = fetchPokemon;